/*  Only used with Theme processes */

html, body {
  &.md-default-theme ,  {
    color: rgba(0,0,0,0.87);
    background-color: color($background, "A100");
  }
}

@mixin checkbox-primary($checkedSelector: '.md-checked') {
  .md-ripple {
    color: color($primary, "600");
  }

  &#{$checkedSelector} .md-ripple {
    color: color($background, "600");
  }

  .md-ink-ripple {
    color: rgba(0,0,0,0.54);
  }

  &#{$checkedSelector} .md-ink-ripple {
    color: rgba(color($primary, "500"), 0.87);
  }

  .md-icon {
    border-color: rgba(0,0,0,0.54);
  }

  &#{$checkedSelector} .md-icon {
    background-color: rgba(color($primary, "500"), 0.87);
  }

  &#{$checkedSelector}.md-focused .md-container:before {
    background-color: rgba(color($primary, "500"), 0.26);
  }

  &#{$checkedSelector} .md-icon:after {
    border-color: rgba(color($primary, "contrast"), 0.87);
  }

  & .md-indeterminate[disabled] {
    .md-container {
      color: rgba(0,0,0,0.26);
    }
  }
}
